import React from 'react';

export default function Card({title, content}) {
    return (
      <div className="bg-white text-black font-medium text-xl rounded-2xl px-8 py-8">
        <p className="">
          {title}
        </p>
        <div className="text-base font-normal mt-2 whitespace-pre-line">
          {content}
        </div>
      </div>
    );
} 